import React , { Component } from 'react'
import AddPageTemplate from '../components/addPageTemplate'
import {message , Form} from 'antd'

import api from '../constants/api'
import instance from '../util/service'
import formdata from '../util/formdata'

class UserAdd extends Component{

    constructor(props){
        super(props);
        this.state={
            roles:[]
        }
    }

    componentDidMount(){
        instance.post(api.GETROLES , 'rows=10').then((data)=>{
            let roles = data.data.rows.map((item , index)=>{
                return {text:item.name,value:item.id}
            })
            this.setState({
                roles:roles
            })
        } , (data)=>{
            message.error(data.msg);
        })
    }

    addCallback = (values)=>{
        instance.post(api.ADDADMIN , formdata(values)).then((data)=>{
            console.log(data);
        } , (data)=>{message.error(data.msg);})
    }

    render(){

        return(
            <div>
                <AddPageTemplate 
                    param={[
                        {title:'用户信息',rows:[
                            {label:'用户姓名',type:'input',placeholder:'请输入用户姓名',ref:'name',rules:[
                                {required:true,message:'请输入用户姓名'}
                            ],span:[6 , 18]},
                            {label:'身份证号',type:'input',placeholder:'请输入身份证号',ref:'accNo',rules:[
                                {required:true,message:'请输入身份证号'},
                                {pattern:/^\d{18}$/,message:'请输入正确的身份证号'}
                            ],span:[6 , 18]},
                            {label:'手机号',type:'input',placeholder:'请输入手机号',ref:'cellPhone',rules:[
                                {required:true,message:'请输入手机号码'},
                                {pattern:/^(13[0-9]|14[5|7]|15[0|1|2|3|5|6|7|8|9]|18[0|1|2|3|5|6|7|8|9])\d{8}$/,message:'请输入正确的手机号码'}
                            ],span:[6 , 18]},
                            {label:'电子邮箱',type:'input',placeholder:'请输入电子邮箱',ref:'email',rules:[
                                {required:true,message:'请输入电子邮箱'},
                                {pattern:/^\w+([-+.]\w+)*@\w+([-.]\w+)*\.\w+([-.]\w+)*$/,message:'请输入正确的电子邮箱'}
                            ],span:[6 , 18]},
                            {label:'qq号码',type:'input',placeholder:'请输入qq号码',ref:'qq',rules:[],span:[6 , 18]},
                            {label:'用户角色',type:'select',placeholder:'请选择用户角色',ref:'role',rules:[
                                {required:true,message:'请选择用户角色'}
                            ],span:[6 , 18],options:this.state.roles},
                            {label:'状态',type:'select',placeholder:'请选择状态',ref:'state',rules:[
                                {required:true,message:'请选择状态'}
                            ],span:[6 , 18],options:[
                                {text:'启用',value:'1'},
                                {text:'禁用',value:'0'}
                            ]},
                            {label:'密码',type:'input',placeholder:'请输入密码',ref:'password',rules:[
                                {required:true,message:'请输入密码'}
                            ],span:[6 , 18]},
                            {label:'确认密码',type:'input',placeholder:'请确认密码',ref:'repassword',rules:[
                                {required:true,message:'请输入确认密码'},
                                {validator:"password"}
                            ],span:[6 , 18]},
                            {label:'备注',type:'textarea',placeholder:'请输入备注',ref:'remark',rules:{},span:[6 , 18]},
                        ]}
                    ]} 

                    callback={this.addCallback}
                />
            </div>
        )
    }

}

export default UserAdd = Form.create()(UserAdd)