import React , { Component } from 'react'

import { Card , Row , Col , Button , Icon , Drawer , message , Tag} from 'antd'

import TableComponent from '../components/table'
import SearchComponent from '../components/search'
import UserAdd from './userAdd'

import instance from '../util/service';
import API from '../constants/api';
import formdata from '../util/formdata';

const ButtonGroup = Button.Group;

class User extends Component{
    componentDidMount(){
        document.title = "用户管理";

        this.getUsers();
    }

    getUsers = (values)=>{
        instance.post(API.GETUSERS , formdata(values)).then((data)=>{
            this.setState({
                data:data.data.rows
            });
        } , (data)=>{message.error(data.msg)});
    }

    constructor(){
        super();
        this.state = {
            columns:[
                {title:'用户名称' , dataIndex:'name'},
                {title:'手机号码' , dataIndex:'cellPhone'},
                {title:'用户角色' , dataIndex:'roles',render:(value , recode)=>{
                    return value[0].name
                }},
                {title:'Email' , dataIndex:'email'},
                {title:'状态' , dataIndex:'state',render:(value , recode)=>{
                    return value ? (<Tag color="green">正常</Tag>) : (<Tag color="volcano">锁定</Tag>);
                }},
                {title:'登录次数' , dataIndex:'loginCount'},
                {title:'日期' , dataIndex:'updateDate'},
                {title:'操作' , dataIndex:'option',render:(value , recode)=>{
                    return(
                        <ButtonGroup>
                            <Button onClick={()=>{
                                this.setState({
                                    visibel: true
                                })
                            }}>修改</Button>
                            <Button onClick={()=>{
                                this.delUser(recode.id);
                            }}>删除</Button>
                        </ButtonGroup>
                    )
                }}
            ],
            data:[],
            visibel:false,
            searchOption:{
                param:[
                    {type:'input' , placeholder:'用户名称' , ref:'name'},
                    {type:'input' , placeholder:'手机号码' , ref:'cellPhone'}
                ],
                buttons:[
                    {text:'新增' , icon:'plus' , fn:()=>{
                        this.setState({
                            visibel:true,
                        })
                    }}
                ]
            }
        }
    }

    onClose=()=>{
        this.setState({visibel:false})
    }

    delUser=(id)=>{
        instance.post(API.DELADMIN , formdata({param:id})).then((data)=>{
            console.log(data);
        } , (data)=>{
            message.error(data.msg)
        })
    }

    render(){
        return(
            <div className='container'>
                <Card>
                    <SearchComponent param={this.state.searchOption} searchFn={this.getUsers}/>
                    <TableComponent 
                        columns={this.state.columns}
                        dataSource={this.state.data}
                    />
                </Card>
                <Drawer
                    width={400}
                    placement='right'
                    closable={false}
                    onClose={this.onClose}
                    visible={this.state.visibel}
                >
                    <UserAdd />
                </Drawer>
            </div>
        );
    }
}

export default User;