import React , { Component } from 'react';
import {Spin} from 'antd'

const boxstyle = {
    width:"100%",
    height:"100%",
    position:'absolute',
    background:'rgba(255,255,255,.5)',
    zIndex:101,
    display:'flex',
    alignItems:'center',
    justifyContent:'center'
}

class Loading extends Component{
    render(){
        if(this.props.isFlag){
            return(
                <div style={{...boxstyle}}><Spin tip={this.props.tip ? this.props.tip : '加载中...'}/></div>
            )
        }else{
            return(<div></div>)
        }
        
    }
}

export default Loading;