import React , { Component } from 'react'
import '../assets/css/login.css';
import instance from '../util/service';
import API from '../constants/api'
import { Input,Icon,Form,Button,message } from 'antd';

import Loading from '../components/loading'

import { connect } from 'react-redux';
import store from '../redux/store/store';

import formdata from '../util/formdata'

import { setToken } from '../redux/action/loginAction'

class Login extends Component{

    handleSubmit =(e)=>{
        e.preventDefault();
        this.props.form.validateFields((err, values) => {
            if (!err) {
                instance.post(API.LOGIN , formdata(values)).then((data)=>{
                    store.dispatch(setToken(data.data.token));
                    this.props.history.push({pathname:'/main/home'});
                } , (data)=>{
                    message.error(data.msg);
                });
            }
        });
    }

    render(){
        const { getFieldDecorator } = this.props.form;
        return(
            <div className="loginBG">
                <Loading isFlag={this.props.loading.isShow} />
                <div className='login-con'>
                    <div className='ivu-card'>
                        <div className='ivu-card-head'>
                            <p><span>欢迎登录</span></p>
                        </div>

                        <div className='ivu-card-body'>
                            <div className='form-con'>
                                <Form onSubmit={this.handleSubmit}>
                                    <div className='inputItem'>
                                        <Form.Item>
                                            {getFieldDecorator('username' , {
                                                rules:[
                                                    {required:true,message:'请输入用户名'}
                                                ]})(
                                                    <Input addonBefore={<Icon type='user' />} placeholder="请输入用户名" />
                                                )
                                            }
                                        </Form.Item>
                                    </div>
                                    <div className='inputItem'>
                                        <Form.Item>
                                            {getFieldDecorator('password' , {
                                                rules:[
                                                    {required:true,message:'请输入密码'}
                                                ]})(
                                                    <Input addonBefore={<Icon type='lock' />} placeholder="请输入密码" type='password'/>
                                                )
                                            }
                                        </Form.Item>
                                    </div>
                                    <div className='inputItem'>
                                        <Form.Item>
                                            <Button type="primary" htmlType="submit" block>登录</Button>
                                        </Form.Item>
                                    </div>
                                </Form>
                                <p className="login-tip">由成都英黎科技有限公司提供</p>
                            </div>
                            
                        </div>
                    </div>
                </div>
            </div>
        )
    }
}

const login = Form.create()(Login);

const mapStateToProps = function(store){
    return {
        loading:store.loadingReducer
    }
    
}

export default connect(mapStateToProps)(login)