import * as types from '../action/action-type'
const initialState = {
    isShow : false,
}

const loadingReducer = (state = initialState , action) => {
    switch(action.type){
        case types.ISLOADING:
            return Object.assign({} , state , {isShow: action.flag})
        default:
            return state
    }
}

export default loadingReducer;