import axios from 'axios';
import globalCode from '../constants/globalCode'

import store from '../redux/store/store'
import {isLoading} from '../redux/action/loadingAction'

import {message} from 'antd'

const instance = axios.create({
    timeout: 10000,
    headers:{
        "Client":'app'
    }
});

instance.defaults.headers.post['Content-Type'] = 'application/x-www-form-urlencoded';
//instance.defaults.headers.post['Client'] = 'app';

//添加请求链接
instance.interceptors.request.use(function(config){
    //发送请求之前做某事，比如加一个loading
    store.dispatch(isLoading(true));
    return config;
} , (error)=>{
    store.dispatch(isLoading(false));
    message.error(error);
});

//添加一个响应拦截器
instance.interceptors.response.use(function(response){
    store.dispatch(isLoading(false));
    //成功
    if(response.data.code === globalCode.success){
        return response.data;
    }else if(response.data.code === globalCode.timeout){
        return Promise.reject({
            msg:'请求超时'
        });
    }else if(response.data.code === globalCode.notuser){
        return Promise.reject({
            msg:'用户不存在'
        })
    }else{
        return Promise.reject({
            msg:'code编码：'+response.data.code
        })
    }
} , (error)=>{
    store.dispatch(isLoading(false));
    return Promise.reject({
        msg:error.message
    });
});

const mapStateToProps = function(store){
    return{
        isShow : store.isShow
    }
}

export default instance;